// Error a nivel de campo
// django-templateforms
ul.errorlist {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
    color: darken(red, 15%);
    font-size: .8rem;
  }
}

// Errores a nivel de formulario global.
// django-templateforms
.alert {
  &.alert-error {
    ul.errorlist {
      margin: 0;
      padding: 0;

      li {
        list-style: none;
        color: rgba(255, 255, 255, .9);
        font-size: 1rem;
      }
    }
  }
}

// Asterisco required
.input-required {
  color: $error-color;
}
