html {
  font-size: 1rem;
  color: color("grey", "darken-1");
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

code {
  background-color: color("grey", "lighten-4");
  color: color("red", "lighten-1");
  font-size: .9rem;
  padding: .2rem;
}

// Font material-icons
i.material-icons {
  &.middle {
    vertical-align: middle !important;
  }
}

// Headers
h1 { font-size: 2.4rem; }
h2 { font-size: 2rem; }
h3 { font-size: 1.8rem; }
h4 { font-size: 1.6rem; }
h5 { font-size: 1.4rem; }
h6 { font-size: 1rem; }
