// Path fuentes font-awesome
$fa-font-path: "../../vendor/font-awesome/fonts" !default;

/**
 * Override src/static/vendor/Materialize/sass/components/_variables.scss
 */

/* 9. Fonts, ruta relativa desde dirname(__file__)
   ========================================================================== */

$roboto-font-path: "../../vendor/Materialize/fonts/roboto/" !default;


/* 1. Colors, añadidos info y warning
   ========================================================================== */

$primary-color: color("purple", "darken-1") !default;
$primary-color-light: lighten($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary-color-light: color("grey", "lighten-4");
$secondary-color: $primary-color-dark !default;

$success-color: color("green", "base") !default;
$error-color: color("red", "base") !default;
$info-color: color("blue", "base") !default;
$warning-color: color("orange", "base") !default;

$link-color: color("light-blue", "darken-1") !default;

// End Override src/static/vendor/Materialize/sass/components/_variables.scss

