// Mensajes de alertas (messages.info|warning|etc)
.alert {
  color: rgba(255, 255, 255, .8);
  margin: 10px;
  padding: 15px 15px 15px 50px;
  position: relative;

  strong {
    font-weight: bold;
  }

  &:before {
    color: black;
    font-family: "FontAwesome";
    font-size: 2.5rem;
    left: 6px;
    opacity: .4;
    position: absolute;
    top: -3px;
  }

  &.alert-error {
    background-color: $error-color;

    &:before {
      content: "\f057";
    }
  }

  &.alert-info {
    background-color: $info-color;

    &:before {
      content: "\f05a";
    }
  }

  &.alert-warning {
    background-color: $warning-color;

    &:before {
      content: "\f071";
    }
  }

  &.alert-success {
    background-color: $success-color;

    &:before {
      content: "\f058";
    }
  }

  // X para cerrar los alerts.
  .close-alert {
    color: #fff;
    cursor: pointer;
    display: inline-block;
    float: right;
    font-size: 1.5em;
    padding: 4px;
    position: relative;
    top: -8px;

    &:hover {
      color: grey;
    }
  }
}
